import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { TILE_VARIANTS } from './export';

@Component({
  tag: 'mdx-date-picker-tile',
  styleUrl: 'tile.scss',
  shadow: true,
})
export class DatePickerTile {
  @Prop({ reflect: true })
  variant: TILE_VARIANTS = TILE_VARIANTS.DATE;

  @Prop({ reflect: true })
  disabled = false;

  @Prop({ reflect: true })
  selected = false;

  @Prop({ reflect: true })
  today = false;

  @Prop()
  content: string;

  @Prop()
  subContent?: string;

  @Prop()
  tileValue: number;

  @Prop()
  tileAriaLabel: string;

  @Event()
  selectTile: EventEmitter<{ content: string; subContent: string; selected: boolean; tileValue: number }>;

  render() {
    return this.variant === TILE_VARIANTS.WEEKDAY ? (
      <th aria-label={this.tileAriaLabel} class="date-picker__tile">
        {this.content && <span class="date-picker__tile-content">{this.content}</span>}
      </th>
    ) : [TILE_VARIANTS.DATE, TILE_VARIANTS.MONTH, TILE_VARIANTS.YEAR].includes(this.variant) ? (
      <td
        aria-label={this.tileAriaLabel}
        class="date-picker__tile"
        onClick={() => {
          this.selected = !this.selected;
          {
            this.selectTile.emit({
              content: this.content,
              subContent: this.subContent || '',
              selected: this.selected,
              tileValue: this.tileValue,
            });
          }
        }}
      >
        {this.content && <span class="date-picker__tile-content">{this.content}</span>}
        {this.subContent && <span class="date-picker__tile-sub-content">{this.subContent}</span>}
      </td>
    ) : (
      this.variant === TILE_VARIANTS.EMPTY && <td class="date-picker__tile"></td>
    );
  }
}
