import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../../icons/exports';

/**
 * @slot title - for the title in the header
 */
@Component({
  tag: 'mdx-date-picker-header',
  styleUrl: 'header.scss',
  shadow: true,
})
export class DatePickerHeader {
  @Prop()
  arrowUpAriaLabel: string;

  @Prop()
  arrowDownAriaLabel: string;

  @Event()
  arrowClick: EventEmitter<'up' | 'down'>;

  render() {
    return (
      <div class="date-picker__header">
        <div class="date-picker__button-container">
          <slot name="title" />
        </div>
        <div class="date-picker__arrow-container">
          <button
            class="date-picker__arrow-container__arrow"
            aria-label={this.arrowUpAriaLabel}
            onClick={() => this.arrowClick.emit('up')}
          >
            <mdx-icon-arrow-up size={ICON_SIZES.SM} />
          </button>
          <button
            class="date-picker__arrow-container__arrow"
            aria-label={this.arrowUpAriaLabel}
            onClick={() => this.arrowClick.emit('down')}
          >
            <mdx-icon-arrow-down size={ICON_SIZES.SM} />
          </button>
        </div>
      </div>
    );
  }
}
