:host .date-picker__tile {
  display: flex;
  align-items: center;
}

:host([variant='date']) .date-picker__tile {
  all: unset;
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--mdx-comp-date-picker-tile-sizing-date);
  width: var(--mdx-comp-date-picker-tile-sizing-date);
  color: var(--mdx-comp-date-picker-tile-day-color-default);
  font: var(--mdx-comp-date-picker-tile-font-date);
  border-radius: var(--mdx-comp-date-picker-tile-border-radius-date);
  cursor: pointer;

  &:hover {
    background-color: var(--mdx-comp-date-picker-tile-day-background-color-hover);
    color: var(--mdx-comp-date-picker-tile-day-color-hover);
  }

  &:disabled {
    background-color: var(--mdx-comp-date-picker-tile-day-background-color-disabled);
    color: var(--mdx-comp-date-picker-tile-day-color-disabled);
    text-decoration: var(--mdx-comp-date-picker-tile-text-decoration-disabled);
  }

  &:focus-visible {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
    outline-offset: 1px;
  }
}

:host([variant='date'][selected]) .date-picker__tile {
  background-color: var(--mdx-comp-date-picker-tile-day-background-color-selected);
  color: var(--mdx-comp-date-picker-tile-day-color-selected);
  cursor: pointer;
}

:host([variant='date'][today]) .date-picker__tile {
  background-color: var(--mdx-comp-date-picker-tile-day-background-color-today);
  color: var(--mdx-comp-date-picker-tile-day-color-today);
  cursor: pointer;
}

:host([variant='date'][today][selected]) .date-picker__tile {
  background-color: var(--mdx-comp-date-picker-tile-day-background-color-selected);
  color: var(--mdx-comp-date-picker-tile-day-color-selected);
  cursor: pointer;
}

:host([variant='year']) .date-picker__tile {
  all: unset;
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--mdx-comp-date-picker-tile-sizing-year);
  width: var(--mdx-comp-date-picker-tile-sizing-year);
  color: var(--mdx-comp-date-picker-tile-year-color-default);
  font: var(--mdx-comp-date-picker-tile-font-year);
  border-radius: var(--mdx-comp-date-picker-tile-border-radius-year);
  cursor: pointer;

  &:hover {
    background-color: var(--mdx-comp-date-picker-tile-year-background-color-hover);
    color: var(--mdx-comp-date-picker-tile-year-color-hover);
  }

  &:disabled {
    background-color: var(--mdx-comp-date-picker-tile-year-background-color-disabled);
    color: var(--mdx-comp-date-picker-tile-year-color-disabled);
    text-decoration: var(--mdx-comp-date-picker-tile-text-decoration-disabled);
  }
  &:focus-visible {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
    outline-offset: 1px;
  }
}

:host([variant='year'][selected]) .date-picker__tile {
  background-color: var(--mdx-comp-date-picker-tile-year-background-color-selected);
  color: var(--mdx-comp-date-picker-tile-year-color-selected);
  cursor: pointer;
}

:host([variant='year'][today]) .date-picker__tile {
  background-color: var(--mdx-comp-date-picker-tile-year-background-color-today);
  color: var(--mdx-comp-date-picker-tile-year-color-today);
  cursor: pointer;
}

:host([variant='year'][today][selected]) .date-picker__tile {
  background-color: var(--mdx-comp-date-picker-tile-year-background-color-selected);
  color: var(--mdx-comp-date-picker-tile-year-color-selected);
  cursor: pointer;
}

:host([variant='month']) .date-picker__tile {
  all: unset;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: var(--mdx-comp-date-picker-tile-sizing-month);
  width: var(--mdx-comp-date-picker-tile-sizing-month);
  color: var(--mdx-comp-date-picker-tile-month-color-default);
  font: var(--mdx-comp-date-picker-tile-font-month);
  text-transform: var(--mdx-comp-date-picker-tile-font-case-uppercase);
  border-radius: var(--mdx-comp-date-picker-tile-border-radius-month);
  cursor: pointer;

  &:hover {
    background-color: var(--mdx-comp-date-picker-tile-month-background-color-hover);
    color: var(--mdx-comp-date-picker-tile-month-color-hover);
  }

  &:disabled {
    background-color: var(--mdx-comp-date-picker-tile-month-background-color-disabled);
    color: var(--mdx-comp-date-picker-tile-month-color-disabled);
    text-decoration: var(--mdx-comp-date-picker-tile-text-decoration-disabled);
  }

  &-sub-content {
    display: flex;
    color: var(--mdx-comp-date-picker-tile-month-font-small-color-default);
    font: var(--mdx-comp-date-picker-tile-font-month-small);
    font-weight: var(--mdx-comp-date-picker-tile-font-weight-emphasis);
    gap: var(--mdx-comp-date-picker-tile-spacing-gap-month-month);

    &:hover {
      color: var(--mdx-comp-date-picker-tile-month-font-small-color-hover);
    }

    &:disabled {
      color: var(--mdx-comp-date-picker-tile-month-font-small-color-disbaled);
    }
  }
  &:focus-visible {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
    outline-offset: 1px;
  }
}

:host([variant='month'][selected]) .date-picker__tile {
  background-color: var(--mdx-comp-date-picker-tile-month-background-color-selected);
  color: var(--mdx-comp-date-picker-tile-month-color-selected);
  cursor: pointer;

  &-sub-content {
    color: var(--mdx-comp-date-picker-tile-month-font-small-color-selected);
  }
}

:host([variant='month'][today]) .date-picker__tile {
  background-color: var(--mdx-comp-date-picker-tile-month-background-color-today);
  color: var(--mdx-comp-date-picker-tile-month-color-today);
  cursor: pointer;

  &-sub-content {
    color: var(--mdx-comp-date-picker-tile-month-font-small-color-today);
  }
}

:host([variant='month'][today][selected]) .date-picker__tile {
  background-color: var(--mdx-comp-date-picker-tile-month-background-color-selected);
  color: var(--mdx-comp-date-picker-tile-month-color-selected);
  cursor: pointer;

  &-sub-content {
    color: var(--mdx-comp-date-picker-tile-month-font-small-color-selected);
  }
}

:host([variant='weekday']) .date-picker__tile {
  all: unset;
  display: flex;
  align-items: center;
  justify-content: center;
  font: var(--mdx-comp-date-picker-tile-font-weekday);
  color: var(--mdx-comp-date-picker-tile-weekday-color-default);
  height: var(--mdx-comp-date-picker-tile-sizing-weekday);
  width: var(--mdx-comp-date-picker-tile-sizing-weekday);
  border-radius: var(--mdx-comp-date-picker-tile-border-radius-weekday);
  text-transform: var(--mdx-comp-date-picker-tile-font-case-uppercase);
  cursor: default;
}

:host([variant='empty']) .date-picker__tile {
  all: unset;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--mdx-comp-date-picker-tile-day-background-color-empty);
  height: var(--mdx-comp-date-picker-tile-sizing-weekday);
  width: var(--mdx-comp-date-picker-tile-sizing-weekday);
  pointer-events: none;
}
