:host .date-picker {
  &__header {
    display: flex;
    font: var(--mdx-comp-date-picker-header-font-title);
    height: var(--mdx-comp-date-picker-header-sizing-height);
    background-color: var(--mdx-comp-date-picker-header-background-color-default);
    color: var(--mdx-comp-date-picker-header-color-default);
    border-bottom: solid;
    border-color: var(--mdx-comp-date-picker-header-border-color-color);
    border-width: var(--mdx-comp-date-picker-header-border-width-bottom);
    display: flex;
    justify-content: space-between;
    padding: 0 var(--mdx-comp-date-picker-header-padding-default);
    border-radius: var(--mdx-comp-date-picker-container-border-radius-default)
      var(--mdx-comp-date-picker-container-border-radius-default) 0 0;
  }

  &__button-container {
    display: flex;
    gap: var(--mdx-comp-date-picker-header-spacing-gap-month-year);

    ::slotted([slot='title']) {
      all: unset;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: var(--mdx-comp-date-picker-header-spacing-gap-icons);
      cursor: pointer;
    }
  }

  &__label-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__arrow-container {
    display: flex;
    align-items: center;
    gap: var(--mdx-comp-date-picker-header-spacing-gap-month-year);

    &__arrow {
      all: unset;
      color: var(--mdx-comp-date-picker-header-icon-color-default);
      cursor: pointer;

      &:focus-visible {
        border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
        outline-color: var(--mdx-comp-keyboard-focus-color-default);
        outline-style: solid;
        outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
        outline-offset: 2px;
      }
    }
  }
}
